function validate() {
    let result = true;
    const modules = items.value.filter(e => e.type == "module" && e.anyChildChecked);
    const appLayerItems = items.value.filter(e => e.type == "qjson" && e.checked);

    startScreen.value = (viewModel.studio.items as Array<IModel>).find(item => item.isRootPage && items.value.find(e => e.id == item.ID)?.checked);
    if(!startScreen.value)
