___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-environment {\n    background-color: transparent;\n    border-radius: 4px;\n    border: 1px solid #BCBCBC;\n    color: #212121;\n    padding-left: 12px;\n}\n.custom-placeholder:empty:before {\n    color: #BCBCBC !important;\n    font-weight: 400;\n}\n.custom-select .items .option {\n    font-size: 14px;\n    padding-left: 12px;\n    line-height: 32px;\n}\n.custom-select .items .option:hover {\n    background-color: #EEEEEE;\n    color: #212121;\n}\n.custom-select .items .option.disabled:hover {\n    background-color: #FFFFFF;\n    color: #212121;\n}\n.custom-select.select-environment .items {\n    margin-top: 4px;\n    color: #212121;\n    border-radius: 4px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    position: static !important;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/publish/publishDialog.vue"],"names":[],"mappings":";AA2nCA;IACI,6BAA6B;IAC7B,kBAAkB;IAClB,yBAAyB;IACzB,cAAc;IACd,kBAAkB;AACtB;AAEA;IACI,yBAAyB;IACzB,gBAAgB;AACpB;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,iBAAiB;AACrB;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;AAEA;IACI,eAAe;IACf,cAAc;IACd,kBAAkB;IAClB,yBAAyB;IACzB,gBAAgB;IAChB,yBAAyB;IACzB,0CAA0C;IAC1C,2BAA2B;AAC/B","sourcesContent":["<template>\n    <div class=\"container\">\n        <div>\n            <CheckboxComp :class=\"['modules-selected-checkbox', props.screenType == 'Deploy' ? 'deploy' : '']\"\n                :minus=\"mainCheckboxState == false\" :check=\"mainCheckboxState == true\"\n                @click=\"handleModuleCountCheckboxEvent(mainCheckboxState == undefined)\" />\n            <div :class=\"['modules-selected-text', props.screenType == 'Deploy' ? 'deploy' : '']\">\n                <span class=\"text-number\">{{  allChildCheckedModulesCount + anyChildCheckedModulesCount + checkedRootLevelModelsCount }}</span>\n                of <span class=\"text-number\">{{ totalRootLevelCount }}</span> items selected\n            </div>\n        </div>\n\n        <div :class=\"['modules-datatable', props.screenType == 'Deploy' ? 'deploy' : '']\">\n            <div v-for=\"item in items\">\n                <div v-if=\"item?.type == 'module'\">\n                    <div class=\"row-container\">\n                        <i :class=\"'mdi mdi-menu' + (item.collapsed ? '-right' : '-down') + ' chevron' + (!item.children || item.children.length == 0 ? ' disabled': '')\"\n                            @click=\"handleCollapse(item)\" />\n                        <CheckboxComp class=\"module-checkbox\" :check=\"item.allChildChecked\"\n                            :minus=\"!item.allChildChecked && item.anyChildChecked\" :disabled=\"item.disabled\"\n                            @click=\"handleItemCheckEvent(item)\" />\n                        <span :class=\"['module-name', item.disabled ? ' disabled': '']\">{{ item.name }}</span>\n                    </div>\n                    <div class=\"row-container\" v-show=\"!item.collapsed\" v-for=\"child in item?.children\">\n                        <CheckboxComp class=\"model-checkbox\" :check=\"child.checked\" :disabled=\"child.disabled\"\n                            @click=\"handleItemCheckEvent(child)\" />\n                        <i v-if=\"child.type == 'qjson'\" class=\"mdi mdi-palette qjson-color model-icon\" />\n                        <i v-if=\"child.type == 'yaml'\" class=\"mdi mdi-cog-outline yaml-color model-icon\" />\n                        <i v-if=\"child.type == 'js' || child?.type == 'css'\"\n                            class=\"mdi mdi-cog-outline js-color model-icon\" />\n                        <i v-if=\"child.type == 'process'\" class=\"mdi mdi-auto-fix process-color model-icon\" />\n                        <i v-if=\"child.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color model-icon\" />\n                        <i v-if=\"child.type == 'entityDesigner'\"\n                            class=\"mdi mdi-database entity-designer-color model-icon\" />\n                        <i v-if=\"child.type == 'flow'\" class=\"mdi mdi-step-forward bpmn-color model-icon\" />\n                        <span :class=\"['model-name', child.disabled ? ' disabled' : '']\">{{ child.name }}</span>\n                        <div v-if=\"!child.usageType\" :class=\"['version-text', child.disabled ? 'disabled' : '']\" @click=\"versionDialog(child)\">\n                            {{ child.version ? child.version : 'No version' }} <i v-if=\"!child.usageType\" :class=\"['mdi mdi-pencil-outline version-icon', child.disabled ? 'disabled' : '']\"></i>\n                        </div>\n                    </div>\n                </div>\n                <div v-else>\n                    <div class=\"row-container\">\n                        <CheckboxComp class=\"module-checkbox\" :check=\"item?.checked\" :disabled=\"item.disabled\"\n                            @click=\"handleItemCheckEvent(item)\" />\n                        <i v-if=\"item?.type == 'qjson'\" class=\"mdi mdi-palette qjson-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'yaml'\" class=\"mdi mdi-cog-outline yaml-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'js' || item?.type == 'css'\"\n                            class=\"mdi mdi-cog-outline js-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'process'\" class=\"mdi mdi-auto-fix process-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'entityDesigner'\"\n                            class=\"mdi mdi-database entity-designer-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'flow'\" class=\"mdi mdi-step-forward bpmn-color app-tier-model-icon\" />\n                        <span class=\"app-tier-model-name\">{{ item?.name }}\n                            <span class=\"start-screen\" v-if=\"startScreen?.ID == item.id\">\n                                <i class=\"mdi mdi-home-outline\"></i>\n                                <span class=\"button-tooltip\" style=\"top: -30px; left: 2px; z-index: 1; width: 80px;\">Start\n                                    Screen</span>\n                            </span>\n                        </span>\n\n                        <span v-if=\"!item.usageType\" :class=\"['version-text', item.disabled ? 'disabled' : '']\" @click=\"versionDialog(item)\">\n                            {{ item.version ? item.version : 'No version' }} <i v-if=\"!item.usageType\" :class=\"['mdi mdi-pencil-outline version-icon', item.disabled ? 'disabled' : '']\"></i>\n                        </span>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div v-if=\"props.screenType == 'Deploy' && props.platformSelection.web\" style=\"margin-bottom: 102px\">\n            <div style=\"display: flex; align-items: center;\">\n                <span class=\"custom-text environment\"> Publish To </span>\n                <span class=\"publish-info\">\n                    <i class=\"mdi mdi-information\"></i>\n                    <span class=\"button-tooltip\" style=\"top: 460px; left: 96px; z-index: 1; width: 231px;\">Publish To\n                        options may change depending on the module type you choose.</span>\n                </span>\n            </div>\n\n            <CustomSelect :options=\"publishTargets\" @input=\"onTargetSelect\" :selectedOption=\"selectedTarget\"\n                :default=\"selectedTarget\" :disabledOptions=\"disabledOptions\" selectedClass=\"selected-environment\"\n                placeholder=\"Select the publish environment of your application\" class=\"select-environment\">\n            </CustomSelect>\n        </div>\n        <div v-if=\"props.screenType == 'Deploy' && !props.platformSelection.web && props.platformSelection.mobile\"\n            class=\"mobile-publish-text\">\n            <i class=\"mdi mdi-information\"></i>\n            <span>Models and static files of your mobile application will be served from Public Cloud.</span>\n\n        </div>\n        <div :class=\"['footer', props.screenType == 'Deploy' ? 'deploy' : 'export']\">\n            <div :class=\"['cancel-button', props.screenType == 'Deploy' ? 'deploy' : '']\" @click=\"close\">Cancel</div>\n            <div v-if=\"props.screenType == 'Export'\" :class=\"(saveButtonDisable ? ' button-disable' : '')\" @click=\"save\">\n                <div class=\"save-button \">DOWNLOAD</div>\n            </div>\n            <div v-if=\"props.screenType == 'Deploy'\" :class=\"[(saveButtonDisable ? ' button-disable' : '')]\" @click=\"save\">\n                <span class=\"save-button deploy\">PUBLISH</span>\n                <span v-if=\"selectedTarget\" class=\"button-tooltip\"\n                    style=\"top: -46px; left: 648px; z-index: 1; width: 120px;\">You need to include your start screen.</span>\n                <span v-if=\"!selectedTarget\" class=\"button-tooltip\"\n                    style=\"top: -46px; left: 644px; z-index: 1; width: 123px;\">You need to select a publish\n                    environment.</span>\n            </div>\n\n        </div>\n    </div>\n</template>\n\n\n<script setup lang=\"ts\">\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IExportViewItem, IModel, IModelHistoryInfo, IModule } from \"../../../../../domain/model/models\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport CheckboxComp from \"../../checkboxComp.vue\";\nimport { ref, computed, defineAsyncComponent, watch, onMounted, onBeforeMount } from \"vue\";\nimport { IDialog } from \"../../../../../domain/presentation/IDialog\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { DeployType, ICreateDeployJobParam, ICreateExportJobParam, IExportItem, IExportJobData, IPlatformSelection, IProcessConfigModelItem } from \"../../../../../../../common/qCloudTemp/exporter\";\nimport { ICreateExportJob } from \"../../../../../domain/useCase/ICreateExportJob\";\nimport { IDeployJobs } from \"../../../../../domain/useCase/IDeployJobs\";\nimport { IQCloudApi } from \"../../../../../domain/infrastructure/IQCloudApi\";\nimport { IPackJobs } from \"../../../../../domain/useCase/IPackJobs\";\nimport { IJobUtils } from \"../../../../../domain/useCase/IJobUtils\";\nimport { IQJsonAdditionals, UsageType } from \"../../../../../../../common/qCloudTemp/quickCloud\";\nimport { PlateauHelper } from \"@stechquick/algae/lib/helpers/plateau/plateauHelper\";\nimport { IViewModelManager } from \"../../../../../domain/viewModel/IViewModelManager\";\nimport CustomSelect from \"../../../components/customSelect.vue\"\nimport { IOrganizationCloud } from \"../../../../../../../common/clean/domain/useCases/ICloudProvider\";\nimport { IListMultiModelVersions } from \"../../../../../domain/useCase/IListMultiModelVersions\";\n\ninterface ITableData {\n    id: string;\n    parentId?: string;\n    collapsed?: boolean;\n    anyChildChecked?: boolean;\n    allChildChecked?: boolean;\n    checked?: boolean;\n    name: string;\n    type: IModel[\"modelType\"] | IModule[\"objectType\"];\n    version?: string;\n    disabled?: boolean | true;\n    children?: Array<ITableData>;\n    usageType?: UsageType;\n}\n\ninterface IDeployScreenAdditionalProps {\n    screenType: \"Export\" | \"Deploy\";\n    platformSelection: IPlatformSelection;\n}\n\ninterface IDisabledOptions {\n    index: number\n    cause: string;\n}\n\nconst props = defineProps<IDeployScreenAdditionalProps>();\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst deployJobsUseCase = container.resolve<IDeployJobs>(IDeployJobs);\nconst qcloudApi = container.resolve<IQCloudApi>(IQCloudApi);\nconst packJobsUseCase = container.resolve<IPackJobs>(IPackJobs);\nconst jobUtils = container.resolve<IJobUtils>(IJobUtils);\nconst emit = defineEmits([\"close\"]);\nconst items = ref<Array<ITableData>>([]);\nconst validations = ref<boolean>(true);\nconst selectedTarget = ref<string>(\"\");\nconst disabledOptions = ref<Array<IDisabledOptions>>();\nconst publishTargets = ref<Array<string>>();\nconst modelHistories = ref<Map<string, Array<IModelHistoryInfo>>>();\nconst startScreen = ref<IModel>();\n\nonMounted(async () => {\n    await initVersions();\n    handleItems();\n    props.screenType == \"Deploy\" && syncPublishTargets();\n    validations.value = validate();\n})\n\nconst allChildCheckedModulesCount = computed(() => items.value.filter(e => e.type == \"module\" && e.allChildChecked).length);\nconst anyChildCheckedModulesCount = computed(() => items.value.filter(e => e.type == \"module\" && !e.allChildChecked && e.anyChildChecked).length);\nconst checkedRootLevelModelsCount = computed(() => items.value.filter(e => e.type != \"module\" && e.checked).length);\nconst totalRootLevelCount = computed(() => items.value.length);\nconst saveButtonDisable = computed(() => (props.screenType == \"Deploy\" && props.platformSelection.web && selectedTarget.value == \"\") || !validations.value || !items.value.some((item) => item.checked || item.anyChildChecked));\nconst mainCheckboxState = computed(() => items.value.length == allChildCheckedModulesCount.value ? true \n        : (anyChildCheckedModulesCount.value + checkedRootLevelModelsCount.value > 0  ? false : undefined));\n\nfunction validate() {\n    let result = true;\n    const modules = items.value.filter(e => e.type == \"module\" && e.anyChildChecked);\n    const appLayerItems = items.value.filter(e => e.type == \"qjson\" && e.checked);\n\n    startScreen.value = (viewModel.studio.items as Array<IModel>).find(item => item.isRootPage && items.value.find(e => e.id == item.ID)?.checked);\n    if(!startScreen.value)\n        return false;\n\n    if (props.screenType == \"Deploy\") {\n        if (modules.length > 0)\n            result = modules\n                .filter(e => e.name != \"processmanager\").map(e => e.children).filter(e => !e?.find(f => f.type == \"entityDesigner\" && f.checked)).length <= 0\n\n        let isApplicationWillDeploy = appLayerItems.length > 0;\n        if (isApplicationWillDeploy) {\n            result = appLayerItems\n                .map(e => viewModel.studio.items.find(item => item.ID == e.id) as IModel)\n                .findIndex(e => ((e.additionals as IQJsonAdditionals).isRootPage)) != -1;\n        }\n    }\n\n    return result;\n}\n\nfunction mobileFilter(model: IModel) {\n    return props.platformSelection.web || !props.platformSelection.mobile ||\n        !(model.modelType == \"bpmn\" || model.modelType == \"entityDesigner\" || model.modelType == \"process\");\n}\n\nasync function initVersions() {\n    const modelIDs = Array<string>();\n    viewModelManager.flatMapItems(viewModel.studio.items, (item, path) => {\n        if (item.objectType != \"model\") { return; }\n        modelIDs.push(item.ID);\n        return item;\n    });\n\n    modelHistories.value = await executor.executeUseCase<IListMultiModelVersions>(IListMultiModelVersions, { modelIDs });\n}\n\nasync function handleItems() {\n    items.value = viewModel.studio.items\n        .filter((item) => item.objectType == \"module\" && item.children.length > 0)\n        .map((item) => {\n            const tableData: ITableData = {\n                id: item.ID,\n                collapsed: true,\n                name: item.name,\n                type: \"module\",\n                children: (item as IModule).children\n                    .filter((child) => child.objectType == \"model\")\n                    .filter(e => !props || props.screenType == \"Export\" || mobileFilter(e as IModel))\n                    .map(child => {\n                        const maxVersion = getItemMaxVersion(child as IModel)\n                        return {\n                            id: child.ID,\n                            parentId: item.ID,\n                            checked: maxVersion != undefined,\n                            disabled: !maxVersion,\n                            name: child.name,\n                            type: (child as IModel).modelType,\n                            version: maxVersion\n                        } as ITableData\n                    })\n            }\n            handleParentCheckedStatus(tableData);\n            return tableData;\n        });\n\n    viewModel.studio.items\n        .filter((item) => item.objectType == \"model\" && item.usageType != \"appSettings\")\n        .forEach((item) => {\n            const maxVersion = getItemMaxVersion(item as IModel)\n            items.value.push(\n                {\n                    id: item.ID,\n                    checked: maxVersion != undefined,\n                    name: item.name,\n                    disabled: !maxVersion,\n                    type: (item as IModel).modelType,\n                    version: maxVersion,\n                });\n        });\n    const uiSettingsID = CryptoHelper.CreateGuid();\n    const appSettings = viewModel.studio.items.filter((item) => item.objectType == \"model\" && item.usageType == \"appSettings\") as Array<IModel>;\n    const uiSettings: ITableData = {\n        id: uiSettingsID,\n        collapsed: true,\n        name: \"UI Settings\",\n        type: \"module\",\n        children: appSettings.map(settings => {\n            const maxVersion = getItemMaxVersion(settings as IModel)\n            return {\n                id: settings.ID,\n                parentId: uiSettingsID,\n                name: settings.name,\n                type: (settings as IModel).modelType,\n                checked: maxVersion != undefined,\n                usageType: settings?.usageType,\n                disabled: !maxVersion,\n                version: maxVersion,\n            }\n        }) as Array<ITableData>,\n    };\n    handleParentCheckedStatus(uiSettings);\n    items.value.unshift(uiSettings);\n}\n\nfunction getItemMaxVersion(item: IModel): string | undefined {\n    if (!modelHistories.value?.has(item.ID))\n        return undefined;\n\n    const versions = modelHistories.value.get(item.ID);\n    if (!versions)\n        return undefined;\n\n    const findMaxVersion = (versions: Array<string>) => {\n        const versionNumbers = versions.map(v => {\n            const parts: Array<string> = v.split(\".\");\n            return parseInt(parts[0]) * 10 + parseInt(parts[1]) * 1;\n        })\n\n        versionNumbers.sort((a, b) => b - a);\n\n        const maxVersion = versionNumbers[0];\n        return `${Math.floor(maxVersion / 10)}.${maxVersion % 10}`;\n    }\n\n    return findMaxVersion(versions.map(e => e.version!));\n\n}\n\nfunction handleModuleCountCheckboxEvent(value: boolean) {\n    items.value.filter(e => !e.disabled).forEach((item) => {\n        item.checked = value;\n        const allItemsCheckable = item.allChildChecked != undefined && item.children && item.children.length > 0 && !item.children.some(c => c.disabled);\n        if (allItemsCheckable) item.allChildChecked = value;\n        if (item.anyChildChecked != undefined) item.anyChildChecked = value;\n        if (item.children) item.children.filter(c => !c.disabled).forEach((child) => (child.checked = value));\n    });\n\n    validations.value = validate();\n    props.screenType == \"Deploy\" && syncPublishTargets();\n}\n\nfunction handleCollapse(item: ITableData) {\n    item.collapsed = !item.collapsed;\n}\n\nfunction handleItemCheckEvent(item: ITableData) {\n    if(item.disabled)\n        return;\n\n    if (item.type != \"module\") {\n        item.checked = !item.checked;\n        if (item.parentId) {\n            const parent = items.value.find((i) => i.id == item.parentId)!;\n            handleParentCheckedStatus(parent);\n        }\n    } else {\n        if (item.children && item.children.filter(c => !c.disabled).length > 0) {\n            if (item.anyChildChecked) {\n                item.children?.filter(c => !c.disabled).forEach((child) => (child.checked = false));\n                item.checked = false;\n            } else {\n                item.children?.filter(e => !e.disabled).forEach((child) => (child.checked = true));\n                item.checked = true;\n            }\n            handleParentCheckedStatus(item);\n        }\n        else {\n            item.checked = !item.checked;\n            item.allChildChecked = false;\n        }\n    }\n\n    validations.value = validate();\n    props.screenType == \"Deploy\" && syncPublishTargets();\n}\n\nfunction handleParentCheckedStatus(item: ITableData) {\n    item.disabled = item.children && (item.children.length == 0 || !item.children?.some(child => !child.disabled))\n    item.allChildChecked = item.children && item.children.length > 0 && !item.children.some((child) => !child.checked || child.disabled);\n    item.anyChildChecked = item.children?.some((child) => child.checked && !child.disabled);\n}\n\nfunction syncPublishTargets() {\n    publishTargets.value = []; disabledOptions.value = [];\n\n    const targets = viewModel.user?.selectedOrganization?.actions?.publish?.targets;\n    if (!targets)\n        return;\n\n    const isSelectAnyItem = items.value.find(e => e.anyChildChecked || e.checked);\n    if (!isSelectAnyItem) {\n        publishTargets.value = Object.entries(targets).map(e => cloudTargetMap(e[0])!);\n        const disabledOptionList = Array<IDisabledOptions>();\n        for (let i = 0; i < publishTargets.value.length; i++) {\n            disabledOptionList.push({ index: i, cause: \"You need to include items to publish.\" })\n        }\n        disabledOptions.value = disabledOptionList;\n        return;\n    }\n\n    const checkedPublishTargets = checkAndSetDisabledCauses(targets);\n    const sortedTargets = checkedPublishTargets.sort((e, f) => (!e.cause === !f.cause) ? 0 : e.cause ? 1 : -1);\n\n    const disabledOptionList = Array<IDisabledOptions>();\n    for (let i = 0; i < sortedTargets.length; i++) {\n        const cause = sortedTargets[i].cause;\n        if (cause) disabledOptionList.push({ index: i, cause })\n    }\n    disabledOptions.value = disabledOptionList;\n    publishTargets.value = sortedTargets.map(e => e.optionName);\n\n    selectedTarget.value = !sortedTargets[0].cause ? sortedTargets[0].optionName : \"\";\n}\n\nfunction checkAndSetDisabledCauses(targets: Record<keyof IOrganizationCloud, boolean>) {\n    const list = Array<{ optionName: string, cause?: string }>();\n\n    Object.keys(targets).forEach(e => {\n        const key = e as keyof IOrganizationCloud;\n        const value = targets[key];\n        const optionName = cloudTargetMap(key)!;\n        if (!value)\n            return list.push({ optionName, cause: \"You are not authorized to publish to this target.\" });\n\n\n        switch (key) {\n            case \"azure\":\n                const traverseSelectedItems = (status: boolean, items?: Array<ITableData>) => {\n                    if (!items || items.length == 0) { return status; }\n                    for (const item of items) {\n                        if (item.checked || item.anyChildChecked) {\n                            if (item.type == \"module\") {\n                                status = traverseSelectedItems(status, item.children);\n                            }\n                            if (item.type == \"entityDesigner\" || item.type == \"bpmn\" || item.type == \"process\") {\n                                status = false;\n                                break;\n                            }\n                        }\n\n                    }\n                    return status;\n                }\n\n                const status = traverseSelectedItems(true, items.value);\n                if (!status)\n                    return list.push({ optionName, cause: \"Entity and process models cannot be published to this target.\" });\n                break;\n            case \"plateauGCP\":\n                if (props.platformSelection.mobile)\n                    return list.push({ optionName, cause: \"This target is not available for mobile publishing.\" });\n                \n                const modulesHasNotAnEDModel =  items.value.find(e => e.type == \"module\" && e.anyChildChecked && !e.children?.some(c => c.type == \"entityDesigner\" && c.checked))\n                if (modulesHasNotAnEDModel)\n                    return list.push({optionName, cause: `This target not available. Selected module '${modulesHasNotAnEDModel.name}' has not any Data Model.`})\n                break;\n        }\n\n        list.push({ optionName });\n    })\n\n    return list;\n}\n\nfunction versionDialog(item: ITableData) {\n    const clickedNode = { id: item.id, state: { checked: item.checked }, text: item.name, type: item.type, version: item.version };\n    const hasAnyVersion = modelHistories.value?.has(item.id);\n    const options = { clickedNode, cb: (version: string) => { item.version = version } } as any;\n    if (hasAnyVersion) {\n        options.versions = modelHistories.value?.get(item.id)\n    }\n    dialog.showDialog(\n        defineAsyncComponent(() => import(\"../../application/modelVersionDialog.vue\")),\n        { closable: true, title: \"Change Version\", width: \"900px\", maxHeight: \"500px\", overflow: \"auto\" }, options\n    );\n}\n\nfunction close() {\n    emit(\"close\");\n}\n\nasync function save() {\n    executor.execute(\n        async () => {\n            const initAsGroup = async () => {\n                const groupID = CryptoHelper.CreateGuid();\n                await initHistory(groupID);\n                await packJob(groupID);\n                return groupID;\n            }\n\n            switch (props.screenType) {\n                case \"Export\":\n                    const groupID = await initAsGroup();\n                    await createExportJob(groupID);\n                    break;\n                case \"Deploy\":\n                    if (props.platformSelection.mobile)\n                        return await createAzureDeploy();\n                    switch (cloudTargetMap(selectedTarget.value!, true) as keyof IOrganizationCloud) {\n                        case \"plateauGCP\":\n                            const groupID = await initAsGroup();\n                            await createDeployJob(groupID);\n                            break;\n                        case \"azure\":\n                            await createAzureDeploy();\n                            break;\n                    }\n            }\n            emit(\"close\");\n        },\n        { loading: true });\n}\n\nasync function createAzureDeploy() {\n    const deployItems: Array<IExportItem> = [];\n    const itemIdObj: Record<string, { item: IModel, path: string }> = {};\n\n    viewModelManager.flatMapItems(viewModel.studio.items, (item, path) => {\n        if (item.objectType != \"model\") { return; }\n        itemIdObj[item.ID] = { item, path };\n        return item;\n    });\n\n    const traverseSelectedItems = (items?: Array<ITableData>) => {\n        if (!items || items.length == 0) { return; }\n        items.filter(e => e.checked || e.anyChildChecked).forEach(item => {\n            if (item.type == \"module\") {\n                traverseSelectedItems(item.children);\n                return;\n            }\n\n            const itemMap = itemIdObj[item.id];\n\n            const deployItem: IExportItem = {\n                id: item.id,\n                name: jobUtils.getFileFullname(itemMap.item),\n                path: itemMap.path,\n                type: jobUtils.getItemType(itemMap.item),\n                ver: item.version || \"latest\",\n                isRootPage: itemMap.item.isRootPage,\n                ownerType: 'application',\n                ownerName: viewModel.studio.appName,\n                size: itemMap.item.size,\n            };\n            deployItems.push(deployItem)\n        });\n    };\n\n    traverseSelectedItems(items.value);\n\n    await deployJobsUseCase.createAppDeployJob(deployItems, props.platformSelection);\n}\n\nasync function initHistory(groupID: string) {\n    return qcloudApi.initPublishHistory({\n        appID: viewModel.studio.appId,\n        ID: groupID,\n        isMultiStep: true,\n        type: props.screenType == \"Deploy\" ? \"publish\" : \"download\"\n    });\n}\n\nfunction isUsingPlateau() {\n    const applications = viewModel.user!.selectedOrganization!.applications;\n\n    if (!applications || (!applications.entity && !applications.process && !applications.bpmn))\n        return false;\n\n    return true;\n}\n\nfunction getItemPath(asset: ITableData, ownerName: string) {\n    if (isUsingPlateau())\n        return \"\";\n    if (asset.type == \"qjson\")\n        return ownerName;\n    return \"\";\n}\n\nfunction isUISettings(asset: ITableData) {\n    return asset.name == \"UI Settings\";\n}\n\n\nasync function packJob(groupID: string) {\n    let exportItemMap = new Map<string, Array<IExportItem>>();\n\n    items.value.forEach((asset) => {\n        if (asset.checked) {\n            //QUI\n            if (asset.type == 'qjson') {\n                const isThisrootPage = viewModel.studio.items.find(i => i.objectType == \"model\" && i.modelType == \"qjson\" && (i.additionals as IQJsonAdditionals).isRootPage && i.ID == asset.id);\n\n                const obj: IExportItem = {\n                    id: asset.id,\n                    ver: asset.version!,\n                    type: jobUtils.getItemType({ usageType: asset.usageType, type: asset.type } as IExportViewItem),\n                    path: \"\",\n                    name: jobUtils.getFileFullname({ usageType: asset.usageType, type: asset.type, name: asset.name } as IExportViewItem),\n                    ownerType: 'application',\n                    ownerName: viewModel.studio.appName,\n                    isRootPage: isThisrootPage ? true : undefined,\n                };\n\n                let applicationModels = exportItemMap.get(viewModel.studio.appName);\n                if (applicationModels && applicationModels.length > 0) {\n                    applicationModels.push(obj);\n                    exportItemMap.set(viewModel.studio.appName, applicationModels);\n                }\n                else\n                    exportItemMap.set(viewModel.studio.appName, [obj]);\n                return;\n            }\n        }\n        if (asset.anyChildChecked && asset.children) {\n            let checkedItems = Array<IExportItem>();\n            asset.children!.forEach(child => {\n                if (!child.checked || child.type == \"module\")\n                    return;\n                const exportItem: IExportItem = {\n                    id: child.id,\n                    ver: child.version!,\n                    type: jobUtils.getItemType({ usageType: child.usageType, type: child.type, name: child.name } as IExportViewItem),\n                    path: !isUISettings(asset) ? getItemPath(child, asset.name) : \"\",\n                    name: jobUtils.getFileFullname({ usageType: child.usageType, type: child.type, name: child.name } as IExportViewItem),\n                    ownerType: isUISettings(asset) || !isUsingPlateau() ? \"application\" : (asset.type === 'module' ? 'module' : 'application'),\n                    ownerName: isUISettings(asset) || !isUsingPlateau() ? viewModel.studio.appName : asset.name\n                };\n                if (exportItem.type == \"entityDesigner\")\n                    exportItem.entityGenerationDomain = viewModel.user!.selectedOrganization!.actions?.entityGenerationDomain;\n                checkedItems.push(exportItem);\n            });\n\n            if (checkedItems.length > 0) {\n                if (!isUISettings(asset) && isUsingPlateau())\n                    exportItemMap.set(asset.name, checkedItems);\n                else {\n                    let applicationModels = exportItemMap.get(viewModel.studio.appName);\n                    if (applicationModels && applicationModels.length > 0) {\n                        applicationModels.forEach(e => checkedItems.push(e));\n                    }\n                    exportItemMap.set(viewModel.studio.appName, checkedItems);\n                }\n            }\n\n        }\n    })\n\n    let jobs = Array<ICreateExportJobParam>();\n    exportItemMap.forEach((v, k) => {\n        jobs.push({\n            appID: viewModel.studio.appId,\n            exportType: \"qui\",\n            selectionData: v,\n            layer: 0,\n            groupID: groupID\n        })\n    })\n\n    await packJobsUseCase.createPackJobs(jobs);\n}\n\nasync function createExportJob(groupID: string) {\n    await executor.executeUseCase<ICreateExportJob>(ICreateExportJob, {\n        appID: viewModel.studio.appId,\n        exportType: \"qui\",\n        selectionData: [],\n        layer: 1,\n        groupID: groupID\n    });\n}\n\nasync function createDeployJob(groupID: string) {\n    //1-(n) L1- deployJob(build)\n    let packages = await qcloudApi.getExportJobsByGroupId({ groupID })\n        .then(jobs => jobs.jobs.filter(e => !isExternalModule(e.jobData as IExportJobData)).map(e => {\n            const jobData = e.jobData as IExportJobData;\n            return {\n                ownerName: jobData.items[0].ownerName,\n                jobID: e.jobID,\n                type: jobData.items[0].ownerType,\n                mainDir: getDeployableArchiveRoot(jobData)\n            }\n        }));\n\n    if (!packages || packages.length <= 0)\n        return;\n\n    let buildJobs = Array<ICreateDeployJobParam>();\n    packages.forEach(pack => {\n        buildJobs.push({\n            appID: viewModel.studio.appId,\n            type: \"build\",\n            package: {\n                type: pack.type as DeployType,\n                name: pack.ownerName!,\n                mainDir: pack.mainDir!,\n                zip: `${pack.jobID}.zip`,\n            },\n            layer: 1,\n            groupID: groupID\n        })\n    })\n\n    await deployJobsUseCase.createBuildJobs(buildJobs);\n\n    //2-(1) L2- deployJob(deployment)\n    let jobParam: ICreateDeployJobParam = {\n        appID: viewModel.studio.appId,\n        type: \"deployment\",\n        deployConfig: {\n            process: {\n                configModelIDs: getProcessConfigList(),\n                enabled: items.value.filter(asset => asset.checked && asset.children && asset.children.filter(child => child.checked && child.type == \"bpmn\")).length > 0\n            }\n        },\n        layer: 2,\n        groupID: groupID\n    }\n\n    await deployJobsUseCase.startDeploymentJob(jobParam);\n}\n\nfunction isExternalModule(jobData: IExportJobData) {\n    //FIXME: external modules may be dynamic\n    return jobData.items[0].ownerType == \"module\" && jobData.items[0].ownerName == \"processmanager\";\n}\n\nfunction getProcessConfigList() {\n    let processConfigList = Array<IProcessConfigModelItem>();\n    items.value.forEach((asset) => {\n        if (asset.checked && asset.children) {\n            const usingService = asset.children.find(c => c.type == \"entityDesigner\");\n            if (!usingService)\n                return;\n\n            const serviceName = PlateauHelper.getServiceNameFromModel(usingService.name);\n            asset.children.forEach(child => {\n                if (child.checked && child.type == \"process\") {\n                    processConfigList.push({\n                        moduleName: serviceName,\n                        modelId: child.id,\n                        modelName: child.name\n                    })\n                }\n            })\n        }\n    })\n    return processConfigList;\n}\n\nfunction getDeployableArchiveRoot(jobData: IExportJobData) {\n    //FIXME: change external modules\n    if (jobData.items[0].ownerName == \"processmanager\")\n        return \"\";\n    if (jobData.items[0].ownerType == \"module\") {\n        let edModel = jobData.items.find(e => e.type == \"entityDesigner\");\n        if (!edModel)\n            throw new Error(\"!!! this module cannot deploy. Because its have not a service\");\n        return PlateauHelper.getServiceNameFromModel(edModel.name);\n    }\n    return PlateauHelper.getQuiPackageNameFromAppName(jobData.app.name);\n}\n\nfunction cloudTargetMap(name: string, pureVal: boolean = false) {\n    const map: Record<keyof IOrganizationCloud, string> = { azure: \"Public Cloud\", plateauGCP: \"Plateau Platform Playground\" };\n    if (pureVal) {\n        return Object.keys(map).find(e => {\n            if (map[e as keyof IOrganizationCloud] === name) {\n                return true;\n            }\n        })\n    } else {\n        let key = Object.keys(map).includes(name);\n        if (!key)\n            return;\n        return map[name as keyof IOrganizationCloud];\n    }\n}\n\nfunction onTargetSelect(value: string) {\n    selectedTarget.value = value\n}\n\n</script>\n\n\n<style scoped>\n.container {\n    padding: 32px 20px 20px;\n}\n\n.settings-container {\n    position: relative;\n    top: 0px;\n    left: 0px;\n}\n\n.custom-text {\n    /* position: absolute; */\n    font-family: 'Roboto';\n    font-style: normal;\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    /* left: 24px; */\n    color: #212121;\n}\n\n/* .custom-text.title{\n    top: 92px;\n}\n\n.custom-text.environment{\n    top: 174px;\n} */\n\n.radio-button {\n    position: absolute;\n    top: 60px;\n    left: 20px;\n    cursor: pointer;\n}\n\n.radio-text {\n    position: absolute;\n    top: 59px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n    user-select: none;\n    cursor: pointer;\n}\n\n.existing-app-url {\n    position: absolute;\n    width: 760px;\n    height: 40px;\n    top: 114px;\n    left: 20px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #bcbcbc;\n    border-radius: 4px;\n    font: normal normal normal 14px/19px Roboto;\n    letter-spacing: 0px;\n    padding: 10px 0px 10px 10px;\n}\n\n.url-error {\n    border: 1px solid red;\n}\n\n.url-disable {\n    opacity: 0.5;\n}\n\n.existing-app-url::placeholder {\n    color: #bcbcbc;\n    user-select: none;\n}\n\n.modules-selected-checkbox {\n    cursor: pointer;\n}\n\n.modules-selected-text {\n    font: normal normal normal 14px/19px Roboto;\n    color: #757575;\n    margin: 0 0 13px 30px;\n    ;\n}\n\n/* .modules-selected-text.deploy {\n    top: 257px;\n} */\n\n.text-number {\n    font: normal normal bold 14px/19px Roboto;\n    color: #212121;\n}\n\n.modules-datatable {\n    width: 760px;\n    height: 288px;\n    border: 1px solid #bcbcbc;\n    overflow-x: hidden;\n    overflow-y: auto;\n    margin-bottom: 20px;\n}\n\n/* .modules-datatable.deploy {\n    top: 286px;\n} */\n\n.modules-datatable::-webkit-scrollbar {\n    width: 4px;\n}\n\n.modules-datatable::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.modules-datatable::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.row-container {\n    width: 100%;\n    height: 36px;\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n}\n\n.chevron {\n    position: absolute;\n    left: 4px;\n    color: #757575;\n    font-size: 24px;\n}\n\n.chevron.disabled {\n    pointer-events: none;\n    color: #D5D5D5;\n}\n\n.module-checkbox {\n    position: absolute;\n    top: 10px;\n    left: 43px;\n    cursor: pointer;\n}\n\n.module-name {\n    position: absolute;\n    top: 9px;\n    left: 69px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.module-name.disabled {\n    color: #D5D5D5;\n}\n\n.model-checkbox {\n    position: absolute;\n    top: 10px;\n    left: 59px;\n    cursor: pointer;\n}\n\n.model-icon {\n    position: absolute;\n    width: 16px;\n    height: 16px;\n    top: 6px;\n    left: 85px;\n}\n\n.app-tier-model-icon {\n    position: absolute;\n    width: 16px;\n    height: 16px;\n    top: 6px;\n    left: 69px;\n}\n\n.app-tier-model-name {\n    position: absolute;\n    top: 10px;\n    left: 87px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.app-tier-model-name.disabled {\n    color: #D5D5D5;\n}\n\n.model-name {\n    position: absolute;\n    top: 9px;\n    left: 103px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.model-name.disabled {\n    color: #D5D5D5;\n}\n\n.version-text {\n    margin: 10px 7px 10px auto;\n    display: table;\n    font: normal normal normal 12px/16px Roboto;\n    color: #757575;\n    cursor: pointer;\n    user-select: none;\n}\n\n.version-text.disabled {\n    pointer-events: none;\n    cursor: pointer;\n}\n\n.version-icon {\n    font: normal normal normal 16px/19px Material Design Icons;\n    color: #D5D5D5;\n}\n\n.version-icon.disabled {\n    font: normal normal normal 16px/19px Material Design Icons;\n    color: #EEEEEE;\n    pointer-events: none;\n    cursor: pointer;\n}\n\n.footer {\n    position: relative;\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: end;\n}\n\n.footer.deploy {\n    position: relative;\n    width: 100%;\n}\n\n.footer.export {\n    margin-top: 102px;\n}\n\n.cancel-button {\n    border-radius: 4px;\n    user-select: none;\n    font-style: normal;\n    font-weight: 500;\n    font-family: \"ROBOTO\";\n    font-size: 15px;\n    color: #757575;\n    padding: 10px 16px;\n    cursor: pointer;\n}\n\n\n\n.save-button {\n    background: #151233 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    user-select: none;\n    font-weight: 500;\n    font-size: 15px;\n    color: #ffffff;\n    padding: 10px 16px;\n    cursor: pointer;\n}\n\n.mobile-publish-text,\n.web-publish-text {\n    padding: 5px 12px;\n    margin-bottom: 52px;\n    width: 100%;\n    color: #757575;\n    font-size: 14px;\n    background: #F5F5FA;\n    border-radius: 4px;\n    display: flex;\n    align-items: center;\n}\n\n.mobile-publish-text i,\n.web-publish-text i {\n    font-size: 24px;\n    color: #151233;\n    margin-right: 4px;\n}\n\n.save-button.deploy {\n    padding: 10px 16px;\n}\n\n.button-disable .save-button {\n    color: #757575;\n}\n\n.button-disable .save-button:hover+.button-tooltip,\n.start-screen:hover .button-tooltip,\n.publish-info:hover .button-tooltip {\n    visibility: visible;\n    transition: opacity 0.3s ease-out 1s;\n    opacity: 1;\n}\n\n.button-tooltip {\n    visibility: hidden;\n    position: absolute;\n    background: #757575;\n    color: #FFFFFF;\n    font-size: 11px;\n    font-family: 'Roboto';\n    text-align: center;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    top: 56px;\n    left: 63px;\n    opacity: 0;\n}\n\n.publish-info {\n    color: #757575;\n    font-size: 20px;\n    margin-left: 10px;\n}\n\n.qjson-color {\n    color: #449dd1;\n}\n\n.yaml-color {\n    color: #449dd1;\n}\n\n.js-color {\n    color: #449dd1;\n}\n\n.process-color {\n    color: #007c77;\n}\n\n.bpmn-color {\n    color: #007c77;\n}\n\n.entity-designer-color {\n    color: #e9724c;\n}\n\n.select-environment {\n    width: 100%;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    margin-top: 4px\n}\n</style>\n\n<style>\n.selected-environment {\n    background-color: transparent;\n    border-radius: 4px;\n    border: 1px solid #BCBCBC;\n    color: #212121;\n    padding-left: 12px;\n}\n\n.custom-placeholder:empty:before {\n    color: #BCBCBC !important;\n    font-weight: 400;\n}\n\n.custom-select .items .option {\n    font-size: 14px;\n    padding-left: 12px;\n    line-height: 32px;\n}\n\n.custom-select .items .option:hover {\n    background-color: #EEEEEE;\n    color: #212121;\n}\n\n.custom-select .items .option.disabled:hover {\n    background-color: #FFFFFF;\n    color: #212121;\n}\n\n.custom-select.select-environment .items {\n    margin-top: 4px;\n    color: #212121;\n    border-radius: 4px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    position: static !important;\n}\n</style>\n"],"sourceRoot":""}]);
